<template>
<div v-if="movie">
  <v-card
    class="indigo ligthen-12 mx-auto my-12"
    max-width="600"
    dark
  >
    <template slot="progress">
      <v-progress-linear
        color="deep-purple"
        height="10"
        indeterminate
      ></v-progress-linear>
    </template>

    <v-img
      height="250"
      v-bind:src="movie.Poster"
      contain
    ></v-img>

    <v-card-title>{{movie.Title}}</v-card-title>

    <v-card-text>
      <v-row
        align="center"
        class="mx-0"
      >
        <v-rating
          :value='parseFloat(movie.imdbRating)'
          color="amber"
          length="10"
          dense
          readonly
          size="14"
        ></v-rating>

        <div class="grey--text ms-4">
         {{movie.imdbRating}} ({{movie.Metascore}})
        </div>

        
        <div class="text-center">
          <v-dialog
            v-model="dialog"
            width="500"
          >
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                color="green"
                dark
                v-bind="attrs"
                v-on="on"
              >
                View Rating
              </v-btn>
            </template>

            <v-card>
              <v-card-title class="text-h5 indigo lighten-1">
                Rating and Review
              </v-card-title>

              <v-card-text v-for="review in movie.Ratings" :key="review.Source" class="p-3 my-3">
                {{review.Source}}: {{review.Value}}
              </v-card-text>

              <v-divider></v-divider>

              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn
                  color="primary"
                  text
                  @click="dialog = false"
                >
                  Close
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </div>


        
      </v-row>

      <div class="my-4 text-subtitle-1">
       {{movie.Actors}}
      </div>

      <div>{{movie.Plot}}</div>
    </v-card-text>

    <v-divider class="mx-4"></v-divider>

    <v-card-text>
      <v-chip-group
      
        active-class="deep-purple accent-4 white--text"
        column
      >
        <v-chip v-for="genre in movie.Genre.split(',')" :key="genre">{{genre}}</v-chip>

      </v-chip-group>
    </v-card-text>

  </v-card>
  </div>
</template>

<script>


export default {
  name: 'Detail',
  mounted(){
      let imdbId= this.$route.params.imdbID
      fetch(`https://www.omdbapi.com/?i=${imdbId}&apikey=87d10179`)
      .then(response => response.json())
      .then(data => {
              console.log(data)
              this.movie = data
            });
      //alert(imdbId)
  },
  data(){
      return{
          movie:null,
          dialog: false,
      }
  }

}
</script>
