<template>
    <v-row>
      <v-col cols="10" >
        <v-text-field label="Enter movie name" dark v-model="searchedMovie"></v-text-field>
      </v-col>
      <v-col cols="2" >
          <v-btn color="primary" block v-on:click="searchMovie">Search</v-btn>
      </v-col>
    </v-row>
</template>

<script>


export default {
  name: 'Search',
  data(){
      return{
          searchedMovie:''
      }
  },
  methods:{
      searchMovie: function(){
          this.$emit('callApi', this.searchedMovie)
      }
  }

}
</script>
