<template>
  <div class="home indigo lighten-3">
    <v-container><!--same as class="container", need this because we do not use bootstrap but we use vuetify-->
     <Search v-on:callApi="callOmdbApi"/>
     <ListItem v-for="movie in movies" :key="movie.imdbID" v-bind:movie="movie" />
    </v-container>
    </div>
</template>

<script>
import Search from '../components/Search.vue'
import ListItem from '../components/ListItem.vue'

export default {
  name: 'Home',
  components: {
    Search,
    ListItem
  },
  
  methods:{
    callOmdbApi: function(searchedMovie){
      fetch(`https://www.omdbapi.com/?s=${searchedMovie}&apikey=87d10179`)
      .then(response => response.json())
      .then(data => {
              console.log(data)
              this.movies = data["Search"]
            });

    }
    
  },
  data(){
    return{
      movies:{}
    }
  }

}
</script>
